import { useState } from "react";
import Markdown from 'react-markdown'

export function ListItems(props){
    let {name}=props;
    let [readme,setReadme] = useState();
    let url = `https://raw.githubusercontent.com/Nicolassmaniotto/${name}/master/README.md`
    // https://raw.githubusercontent.com/facebook/react/master/README.md
    console.log(url)
    const getDataText= async ()=>{
        if(!readme){
            const response = await fetch(
                url
               ).then((response) => response.text())
               if(response === '404: Not Found'){
                console.log(response)
                url = `https://raw.githubusercontent.com/Nicolassmaniotto/${name}/master/readme.md`
                getDataText()
               }
            setReadme(response==='404: Not Found'?"":response)
        }else{
            setReadme('')
        }
      }
    return(
    <>
    <div className="lista"  key={"Home-li"+name}>
        <div className={"lista d-flex align-items-baseline"}>
        <a href={"#action"} onClick={()=>{getDataText()}} className='px-2'><h2>{name}</h2></a>
        <a href={`https://github.com/Nicolassmaniotto/${name}`} onClick={()=>{getDataText()}} className=''><h4>GitHub</h4></a>
        </div>
     </div>
     <div className=" d-flex justify-content-center text-light">
        {(!readme)?"":<div className="readme"  key={"Home-li-ul-result-"+name}><Markdown>{readme}</Markdown></div>}
     </div>
    </>
// 

    )
}

